.item {

    margin: 20px;
    min-width: 200px;
    // background-color: #947b9a;
    background: rgba(0,0,0,0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    flex-direction: column;
    filter: drop-shadow(0 10px 8px rgb(0 0 0 / 0.04)) drop-shadow(0 4px 3px rgb(0 0 0 / 0.1));
    @media (max-width: 600px) {
        min-width: 130px;

    }

    .item-content {
        width: 180px;
        // height: 180px;
        margin: 20px 20px 10px 20px;
        border-radius: 10px;
        overflow: hidden;
        @media (max-width: 600px) {
            width: 110px;
            margin: 10px;
        }
    }

    .item-a {
        display: block;
        height: 100%;
    }

    img {
        width: 100%;
        object-fit: contain;
    }

    .title {
        color: #fff;
        text-decoration: none;
        margin-bottom: 10px;
        background: rgba(255,255,255,0.2);
        padding: 2px 11px;
        border-radius: 18px;
        line-height: 1.5;
        display: flex;
        font-weight: 500;
        font-size: 10px;
        align-items: center;
    }
}