$ts: #b9d2b5;
$fless: #f4cb8d;
$sn: #d1b2d2;
$red: #823549;
$nen: #b5e9f6;
$rep: #847e80;
$lover: #f9b2d0;
$flore: #cfcac6;
$em: #c8ae95;
$mn: #434961;

.home {
    display: flex;
    justify-content: center;
    position: relative;
    flex-direction: column;
    align-items: center;
    .content {
        width: 100%;
        margin-left: auto;
        @media (max-width: 800px) {
            width: 100%;
            &.closed {
                left: -280px;
            }
            &.hidden {
                left: -280px;
            }
        }
    }
}

.header {
    width: 100%;
    height: 400px;
    background-position: center;
    font-family: 'PistilliRoman';
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: #fff;
    text-align: center;

    h1, h2, h3 {
        filter: drop-shadow(0px 0px 4px #000);
    }

    h1 {
        margin: 20px;
        font-size: 76px;
        filter: drop-shadow(4px 4px 12px #000);
    }
    h2, h3 {
        margin: 10px;
    }
}

.section {
    font-size: 30px;
    text-transform: uppercase;
    letter-spacing: 2px;
    width: 100%;
    min-height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    > * {
        
        padding: 20px;
        // text-align: center;
    }
}

.section1 {
    background-color: rgba($lover, 0.5);
}
.section2 {
    background-color: rgba($rep, 0.5);
}
.section3 {
    background-color: rgba($ts, 0.5);
}
.section4 {
    background-color: rgba($fless, 0.5);
}